@import "../../assets/utils/variables";

.component {
  top: 0;
  position: sticky;
  box-shadow: 0px 2px 8px rgba(24, 25, 27, 0.08);
  z-index: 4;
  flex: fixed;
  background: $c-white;
  padding-top: 16px;
  padding-bottom: 16px;
  // @extend .contentWrapper;
  // @include transition_all;
  min-width: auto;

  //   @include respond-to(medium) {
  //     padding-left: 24px;
  //     padding-right: 24px;
  //   }

  //   @include respond-to(huge) {
  //     padding-left: 24px;
  //     padding-right: 24px;
  //     //padding-top: 40px;
  //     //padding-bottom: 40px;
  //   }
}

.layerContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-flow: row wrap;
  flex-wrap: nowrap;
}

.userAndMenuContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.selectRoute {
  color: #ff7816;
}

.headerName {
  margin-right: 15px;
  font-size: 18px;
  word-wrap: break-word;
  color: #263543;
  margin-left: 15px;
  white-space: normal;
  text-align: right;
  text-transform: uppercase;
  font-weight: 500;
}

@media screen and (max-width: 1100px) {
  .headerName {
    font-size: 14px;
  }
}

