@import "../../../assets/utils/variables";

.header_user {
  display: flex;
  align-items: center;
  margin-right: 24px;
}

.header_user_title {
  font-size: 16px;
  font-weight: 400;
  color: $c-black_text;
  line-height: 24px;
  white-space: nowrap;
  margin-left: 24px;

  span {
    display: block;
  }
}

.header_more_img {
  margin-left: 24px;
}

.header-user {
  align-items: center;
  box-sizing: border-box;
  justify-content: flex-end;
}

.layout {
  display: flex;
  position: relative;
  flex-direction: row;
  box-sizing: border-box;
  justify-content: flex-start;
  align-items: stretch;
  width: 100%;
  height: auto;
}

.header-user_img {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  min-width: 48px;
  background-color: $c-grey;
  color: $c-blue_text;
  border-radius: 100%;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
}
