@import "../../../assets/utils/variables";

.header_logo {
  display: flex;
  align-items: center;
}

.header_logo_img {
  width: 48px;
  display: flex;
  margin-right: 24px;
  margin-left: 32px;
}

.header_logo_title {
  font-size: 24px;
  font-weight: 700;
  color: $c-black_text;
  line-height: 32px;
  text-transform: uppercase;
  white-space: nowrap;

  .header_logo_version {
    font-size: 12px;
    text-transform: none;
    color: $c-grey_text;
    font-weight: 400;
    line-height: 16px;
  }

  span {
    display: block;
  }
}

@media screen and (max-width: 1100px) {
  .header_logo__title {
    font-size: 14px;
  }
}
